.ViewArticle{
  min-height: 100vh;
  margin-top: -5vh;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  flex-grow: 1;
  background-color: rgb(255, 255, 255);
}

.ViewArticle > div{
  width: 100%;
}

.ViewArticleTitle{
  min-height: 20vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 4rem;
  color: white;
  font-weight: bold;
  -webkit-text-stroke: 2px black;
}

.ViewArticleContent{
  min-height: 40vh;
  position: relative;
  display: flex;
  align-items: center;
  overflow: hidden;
  flex-wrap: wrap;
}

.ViewArticleContent:nth-child(2n){
  justify-content: flex-start;
}

.ViewArticleContent:nth-child(2n + 1){
  justify-content: flex-end;
}

.ViewArticleText{
  position: relative;
  height: max-content;
  min-height: 20vh;
  flex-grow: 1;
  width: 625px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.ViewArticleTextContent{
  font-size: 1.25rem;
  position: relative;
  margin: 20px;
  height: fit-content;
  width: fit-content;
  max-width: 800px;
  text-align: center;
}

.ViewSideStyle{
  background-color: rgb(44, 255, 255);
  position: absolute;
  transform: translate(0, -50%);
  height: 80%;
  top: 50%; 
  left: 0%;
  width: 100%;
}

.ViewFirstTriangle {
  background-color: rgb(0, 200, 200);
  position: absolute;
  height: 80%;
  width: 100%;
  bottom: 10%;
  left: 0px;
  -webkit-clip-path: polygon(0 50%, 0% 100%, 100% 100%);
  clip-path: polygon(0 50%, 0% 100%, 100% 100%);
}

.ViewSecondTriangle {
  background-color: rgb(0, 200, 200);
  position: absolute;
  height: 80%;
  width: 100%;
  bottom: 10%;
  right: 0px;
  -webkit-clip-path: polygon(100% 50%, 0% 100%, 100% 100%);
  clip-path: polygon(100% 50%, 0% 100%, 100% 100%);
}

.ViewArticleDummy{
  position: relative;
  height: 40vh;
  width: max(min(50rem, calc(100% - 32vh)), 400px);
  max-width: max(400px, 80vw);
  flex-grow: 1;
}

.ViewArticleImgFirst, .ViewArticleImgSecond{
  height: max(min(40vw,75%), min(225px,55vw));
  aspect-ratio: 1.5;
  position: absolute;
  top: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  transform-style: preserve-3d;
  transition: transform 500ms; 
}

.ViewArticleImgFirst{
  right: 1rem;
  transform: translateY(-50%) perspective(40vh) rotateX(-10deg) rotateY(20deg);
}

.ViewArticleImgSecond{
  left: 1rem;
  transform: translateY(-50%) perspective(40vh) rotateX(-10deg) rotateY(-20deg);
}

.ViewArticleImgFirst > img, .ViewArticleImgSecond > img{
  width: 100%;
  height: 84.375%;
  transition: box-shadow 500ms;
}

.ViewArticleImgFirst > img{
  -webkit-box-shadow: -3px -3px 10px 1px #000000; 
  box-shadow: -3px -3px 10px 1px #000000;
}

.ViewArticleImgFirst:hover{
  transform: translateY(-50%) perspective(40vh) rotateX(0deg) rotateY(0deg);
}

.ViewArticleImgFirst:hover > img{
  -webkit-box-shadow: 0px 0px 10px 3px #000000; 
  box-shadow: 0px 0px 10px 3px #000000;
}

.ViewArticleImgSecond:hover{
  transform: translateY(-50%) perspective(40vh) rotateX(0deg) rotateY(0deg);
}

.ViewArticleImgSecond:hover > img{
  -webkit-box-shadow: 0px 0px 10px 3px #000000; 
  box-shadow: 0px 0px 10px 3px #000000;
}


.ViewArticleImgSecond > img{
  -webkit-box-shadow: 3px -3px 10px 1px #000000; 
  box-shadow: 3px -3px 10px 1px #000000;
}